import React, { useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom'; //     
import { GoogleLogin } from '@react-oauth/google';
import { toast } from 'react-toastify';
import './css/login.css';

function Login() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate(); //    

  const handleGoogleSignup = async (credentialResponse) => {
    try {
        const res = await axios.post('http://localhost:8080/users/googleLogin', {
            token: credentialResponse.credential,
        },
        {
          headers: {
            "Content-Type": "application/json"
          },
          withCredentials: true
        }
      );

      if (res.status === 200) {
        toast.success(' !');
        console.log(res.data); //    

        //    (: localStorage)
        localStorage.setItem('user', JSON.stringify(res.data));

        //  
        setTimeout(() => {
          navigate('/');
        }, 1000);
      }
    } catch (err) {
        console.error(err);
        alert("  ");
    }
  };

  const handleLogin = async (e) => {
    e.preventDefault();
  
    try {
      const response = await axios.post(
        'http://localhost:8080/users/loginUser', 
        new URLSearchParams({
          email: username,
          user_password: password,
        }),
        {
          headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
          withCredentials: true,
        }
      );

      if (response.status === 200) {
        toast.success(' !');
        console.log(response.data); //    

        //    (: localStorage)
        localStorage.setItem('user', JSON.stringify(response.data));

        //  
        setTimeout(() => {
          navigate('/');
        }, 1000);
      }
    } catch (err) {
      setError(' :    .');
      console.error(err);
    }
  };

  return (
    <div className="container">
      <div className="text-center">
          <h1>   </h1>
      </div>
      <form onSubmit={handleLogin}>
          <div className="form-group">
              <input 
                  type="text"
                  className="form-control" 
                  placeholder="*" 
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                  required 
              />
          </div>
          <div className="form-group">
              <input 
                  type="password" 
                  className="form-control" 
                  placeholder="*" 
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required 
              />
          </div>
          {error && <div className="error-message">{error}</div>}
          <button type="submit" className="btn btn-primary"></button>
          
          <div className="find-account-links">
              <Link to="/findaccount" className="small">  |  </Link>
          </div>
          
          <div className="divider"></div>
          
          <GoogleLogin
            onSuccess={(credentialResponse) => {
              console.log('  ', credentialResponse);
              handleGoogleSignup(credentialResponse); //    
            }}
            onError={() => {
              console.log('  ');
            }}
          />
      </form>
      <div className="text-center footer-links">
          <Link to="/terms" className="small"></Link>
          <span className="divider-vertical">|</span>
          <Link to="/privacy" className="small">  </Link>
      </div>
    </div>
  );
}

export default Login;
