import React, { useEffect, useState } from 'react';
import axios from 'axios';
import NavBar from '../components/NavBar.js';
import './css/admin.css';
const PAGE_SIZE = 10;
axios.defaults.baseURL = 'http://localhost:8080';
axios.defaults.withCredentials = true;
export default function AdminPage() {
  const [users, setUsers] = useState([]);
  const [totalUsers, setTotalUsers] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchKeyword, setSearchKeyword] = useState('');

  const fetchUsers = async (page) => {
    try {
      const response = await axios.get(`/users/admin/users?page=${page}&size=${PAGE_SIZE}`);
      setUsers(response.data.users);
      setTotalUsers(response.data.total);
    } catch (error) {
      console.error('   :', error);
    }
  };

  const deleteUser = async (userId) => {
    if (window.confirm('   ?')) {
      try {
        console.error(userId)
        await axios.delete(`/users/admin/users/${userId}`);
        fetchUsers(currentPage);
      } catch (error) {
        console.error('  :', error);
      }
    }
  };
  const handleLevelChange = async (userId, newLevel) => {
    try {
      await axios.patch(
        `/users/admin/users/${userId}/membership`,
        { membership_level: newLevel },
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      );
      alert("  .");
      fetchUsers(currentPage);
    } catch (error) {
      console.error("   :", error);
      alert("  ");
    }
  };
  
  useEffect(() => {
    fetchUsers(currentPage);
  }, [currentPage]);

  const totalPages = Math.ceil(totalUsers / PAGE_SIZE);

  return (
    <>
    <NavBar />
    <div className="container">
      <h2 className="section-title"> </h2>
      <div className="search-row">
      <input
        type="text"
        className="search-input"
        placeholder="   "
        value={searchKeyword}
        onChange={(e) => setSearchKeyword(e.target.value)}
      />
    </div>

      <table className="admin-table">
        <thead>
          <tr>
            <th> ID</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {users
          .filter(user =>
            user.email.toLowerCase().includes(searchKeyword.toLowerCase()) ||
            user.name?.toLowerCase().includes(searchKeyword.toLowerCase())
          )
          .map(user => (

            <tr key={user.user_id}>
              <td>{user.user_id}</td>
              <td>{user.email}</td>
              <td>
              <select
              className="dropdown-select"
              value={user.membership_level}
             onChange={(e) => handleLevelChange(user.user_id, e.target.value)}
              >
                <option value="regular"></option>
                <option value="business"></option>
                <option value="admin"></option>
              </select>
              </td>
              <td>{user.join_date}</td>
              <td>
                <button className="btn btn-danger" onClick={() => deleteUser(user.user_id)}></button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <div className="pagination">
        {[...Array(totalPages)].map((_, i) => (
          <button
            key={i}
            className={`page-btn ${currentPage === i + 1 ? 'active' : ''}`}
            onClick={() => setCurrentPage(i + 1)}
          >
            {i + 1}
          </button>
        ))}
      </div>
    </div>
    </>
  );
}
