import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './css/signup2.css'; //  

export default function Signup2() {
    const navigate = useNavigate();

    const [formData, setFormData] = useState({
        user_id: null,
        gender: '',
        age: '',
        height: '',
        user_weight: '',
        user_address: '',
        health_goal: ''
    });

    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');

    //     user_id 
    useEffect(() => {
        const storedUserId = localStorage.getItem('user_id');
        if (storedUserId && !isNaN(storedUserId)) {
            setFormData(prev => ({
                ...prev,
                user_id: parseInt(storedUserId, 10)
            }));
        } else {
            setError('   ID.  .');
        }
    }, []);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: value
        }));
    };

    const handleAddressSearch = () => {
        new window.daum.Postcode({
            oncomplete: function (data) {
                const address = data.address;
                setFormData(prev => ({ ...prev, user_address: address }));
            }
        }).open();
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError('');
        setSuccess('');

        if (!formData.user_id) {
            setError('   .  .');
            return;
        }

        try {
            await axios.post('http://localhost:8080/users/details', formData);
            setSuccess('   !');
            setTimeout(() => navigate('/login'), 1000);
        } catch (err) {
            setError('  :  .');
            console.error(err);
        }
    };

    return (
        <div className="container">
            <div className="text-center">
                <h1>   </h1>
            </div>
            <form onSubmit={handleSubmit}>
                <div className="form-group">
                    <label></label>
                    <select
                    name="gender"
                    value={formData.gender}
                    onChange={handleChange}
                    className="form-control"
                    required
                    >
                      <option value=""></option>
                      <option value="1"></option>
                      <option value="2"></option>
                      <option value="3"></option>
                    </select>
                </div>

                <div className="form-group">
                    <label></label>
                    <input
                        type="number"
                        name="age"
                        value={formData.age}
                        onChange={handleChange}
                        className="form-control"
                        required
                    />
                </div>

                <div className="form-group">
                    <label>(cm)</label>
                    <input
                        type="number"
                        name="height"
                        value={formData.height}
                        onChange={handleChange}
                        className="form-control"
                        required
                    />
                </div>

                <div className="form-group">
                    <label>(kg)</label>
                    <input
                        type="number"
                        name="user_weight"
                        value={formData.user_weight}
                        onChange={handleChange}
                        className="form-control"
                        required
                    />
                </div>

                <div className="form-group">
                    <label></label>
                    <div className="input-group">
                        <input
                            type="text"
                            name="user_address"
                            value={formData.user_address}
                            onChange={handleChange}
                            className="form-control"
                            readOnly
                        />
                        <div className="input-group-append">
                            <button
                                type="button"
                                className="btn btn-outline-secondary"
                                onClick={handleAddressSearch}
                            >
                                 
                            </button>
                        </div>
                    </div>
                </div>

                <select
                  name="health_goal"
                  value={formData.health_goal}
                  onChange={handleChange}
                  className="form-control"
                  required>
                    <option value=""></option>
                    <option value="1"> </option>
                    <option value="2"> </option>
                    <option value="3"> </option>
                    </select>


                {error && <div className="alert alert-danger mt-3">{error}</div>}
                {success && <div className="alert alert-success mt-3">{success}</div>}

                <button type="submit" className="btn btn-primary mt-4 w-100"></button>
            </form>
        </div>
    );
}
