import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import NavBar from './NavBar';
import './css/mypage.css';
import InputMask from 'react-input-mask';

export default function Mypage() {
    const [userInfo, setUserInfo] = useState(null);
    const [error, setError] = useState('');
    const [phone, setPhone] = useState('');
    const [formData, setFormData] = useState({ age: '', weight: '', height: '', address: '' });
    const navigate = useNavigate();

    const handleAddressSearch = () => {
        if (window.daum && window.daum.Postcode) {
            new window.daum.Postcode({
                oncomplete: function (data) {
                    const address = data.address;
                    setFormData(prev => ({ ...prev, address }));
                }
            }).open();
        } else {
            alert("     .   .");
        }
    };

    useEffect(() => {
        const fetchUserInfo = async () => {
            try {
                const response = await axios.post("http://localhost:8080/users/myPage", {}, {
                    withCredentials: true
                });

                if (response.status === 200) {
                    setUserInfo(response.data);
                    setFormData({
                        age: response.data.age,
                        weight: response.data.user_weight,
                        height: response.data.height,
                        address: response.data.user_address || ''
                    });
                }
            } catch (err) {
                setError('   .');
                console.error(err);
            }
        };

        fetchUserInfo();
    }, []);

    useEffect(() => {
        if (userInfo?.phone) {
            setPhone(userInfo.phone);
        }
    }, [userInfo]);

    const handleLogout = async () => {
        try {
            await axios.post('http://localhost:8080/users/logout', 
                new URLSearchParams({
                    user_id: userInfo.user_id,
                }),
                {
                    headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                    withCredentials: true,
                }
            );

            localStorage.removeItem('user');
            navigate('/login');
        } catch (err) {
            console.error(' :', err);
            alert('   .');
        }
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({ ...prev, [name]: value }));
    };

    const handleSave = async () => {
        try {
            const payload = new URLSearchParams({
                user_id: userInfo.user_id,
                user_name: userInfo.user_name,
                phone,
                age: formData.age,
                user_weight: formData.weight,
                height: formData.height,
                user_address: formData.address
            });

            const response = await axios.post('http://localhost:8080/users/updateUser', payload, {
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                withCredentials: true
            });

            if (response.status === 200) {
                alert('  .');
            }
        } catch (err) {
            console.error(' :', err);
            alert('    .');
        }
    };

    return (
        <>
            <NavBar />
            <div className="mypage-grid-container">
                <div className="mypage-grid-row profile">
                    <div className="profile-sidebar">
                        {userInfo && (
                            <>
                                <div className="profile-picture">
                                    <img src="/default-profile.png" alt="" />
                                </div>
                                <h3>{userInfo.user_name}</h3>
                                <p>{userInfo.email}</p>
                                <p>: {userInfo.membership_level}</p>
                            </>
                        )}
                    </div>
                </div>

                <div className="mypage-grid-row info">
                    <div className="mypage-content">
                        {error && <p className="error-message">{error}</p>}
                        {userInfo && <h2 className="mypage-title"> </h2>}

                        <div className="form-section">
                            <div className="form-pair">
                                <div className="form-group">
                                    <label></label>
                                    <input
                                        type="text"
                                        value={userInfo?.user_name || ''}
                                        disabled
                                    />
                                    <small className="helper-text">   .</small>
                                </div>
                                <div className="form-group">
                                    <label></label>
                                    <InputMask
                                        mask="999-9999-9999"
                                        value={phone}
                                        onChange={(e) => setPhone(e.target.value)}
                                    >
                                        {(inputProps) => <input type="text" {...inputProps} />}
                                    </InputMask>
                                    <small className="helper-text">: 010-1234-5678</small>
                                </div>

                                <div className="form-group">
                                    <label htmlFor="gender"></label>
                                    <select id="gender" defaultValue={userInfo?.gender} disabled>
                                        <option value="1"></option>
                                        <option value="2"></option>
                                    </select>
                                    <small className="helper-text">   .</small>
                                </div>
                            </div>

                            <div className="form-group full-width">
                                <label htmlFor="address"></label>
                                <div className="address-input-wrap">
                                    <input
                                        type="text"
                                        name="address"
                                        value={formData.address}
                                        onChange={handleInputChange}
                                    />
                                    <button
                                        type="button"
                                        className="address-button"
                                        onClick={handleAddressSearch}
                                        > 
                                         
                                    </button>
                                </div>
                                <small className="helper-text">  .</small>
                            </div>


                            <div className="form-triple">
                                <div className="form-group">
                                    <label htmlFor="age"></label>
                                    <input
                                        type="number"
                                        name="age"
                                        value={formData.age}
                                        onChange={handleInputChange}
                                        inputMode="numeric"
                                        pattern="\d*"
                                        min="0"
                                    />
                                    <small className="helper-text">  .</small>
                                </div>

                                <div className="form-group">
                                    <label htmlFor="weight"> (kg)</label>
                                    <input
                                        type="number"
                                        step="0.01"
                                        name="weight"
                                        value={formData.weight}
                                        onChange={handleInputChange}
                                        inputMode="numeric"
                                        pattern="\d*"
                                        min="0"
                                    />
                                    <small className="helper-text">: 65.5</small>
                                </div>

                                <div className="form-group">
                                    <label htmlFor="height"> (cm)</label>
                                    <input
                                        type="number"
                                        step="0.01"
                                        name="height"
                                        value={formData.height}
                                        onChange={handleInputChange}
                                        inputMode="numeric"
                                        pattern="\d*"
                                        min="0"
                                    />
                                    <small className="helper-text">: 175.2</small>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="mypage-grid-row actions">
                    <div className="button-group">
                        <button onClick={handleSave} className="mypage-button"></button>
                        <button onClick={() => navigate('/change-password')} className="mypage-button secondary"> </button>
                        {userInfo && userInfo.membership_level !== 'regular' && (
                            <button onClick={() => navigate('/meal-plan')} className="mypage-button secondary"> </button>
                        )}
                        <button onClick={handleLogout} className="mypage-button logout"></button>
                    </div>
                </div>
            </div>
        </>
    );
}
